/*---Global-----------------------------------------------------------------------------*/
.App {
  text-align: center;
}
body{
  font-family: 'Karla', sans-serif !important;
}
.azul{
  color:#3b3a8f;
}

.rojo{
  color:#c21b1c;
}

/*---Pre Navbar-----------------------------------------------------------------------------*/
.pre-wrapper{
  background-color: #3b3a8f;
  position: fixed;
  width: 100%;
  z-index: 1030;
}
.pre-nav{
  height: 50px;
  color:white;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
#search{
   position:fixed;
   right:5%;
   top:72px;
   z-index: 1031;
  transition: top 0.6s;
}
@media only screen and (max-width: 990px) {
  .pre-nav{
    align-items: center;
    height: 50px;
    font-size: 1.4vw;
  }
  #search{
    top:110px;
  }
}

/*---Navigation bar-----------------------------------------------------------------------------*/
.ant-menu-item-selected,.ant-menu-item-active,.ant-menu-submenu-open,.ant-menu-submenu-selected,.ant-menu-submenu:hover,.ant-menu-submenu-title:hover{
  border-bottom: 2px solid transparent !important;
  color:#c21b1c !important;
  -webkit-transition:none !important;
  transition:none !important;

}
.ant-menu-submenu-title {
  -webkit-transition:none !important;
  transition:none !important;

}
.navbar-fixed-top{
  transform: translateY(50px);
  transition: transform 0.6s;
  border-bottom: 3px #c21b1c solid;
}
li{
  font-size: 15px;
  font-weight: bold;
}
.navbar-img{

  width: 180px;
  height: 60px;
  background-size:  100% 100%;
  background-image: url(./logo_text.PNG);
}
.navbar-img:hover{
  cursor: pointer;
}
@media only screen and (min-width: 768px) and (max-width: 990px) {


  .navbar-img{
    width: 90px !important;
    height: 30px !important;
  }
  .ant-menu-item,.ant-menu-submenu,.ant-menu-item-selected,.ant-menu-item-active,.ant-menu-submenu-open,.ant-menu-submenu-selected,.ant-menu-submenu:hover,.ant-menu-submenu-title:hover{
    width:auto;
  }

}
#bar-buttons {
  position: absolute;
  left: 0px;
  height: 40px;
  width: 100%;
  -webkit-display: flex;
  display: flex;
  bottom:0;
  justify-content: center;
}
#navbar, .navbar{
  background: #fff;
}

#navbar-menu{
  border:none;
}
@media only screen and (max-width: 767px) {

  .ant-menu-item,.ant-menu-submenu,.ant-menu-item-selected,.ant-menu-item-active,.ant-menu-submenu-open,.ant-menu-submenu-selected,.ant-menu-submenu:hover,.ant-menu-submenu-title:hover{
    width:60%;
    background: white !important;
  }
  #bar-buttons {
    position: static;
    bottom:auto;
    height: auto;
    width: auto;
  }
}
.downnav{
  transform:translateY(0) !important;
}
.downnav-search{
  top:15px !important;
}
.containesr{
  background: url(https://images.unsplash.com/photo-1519389950473-47ba0277781c?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=47d2b1f32cc05c976af4a39256e01cba&auto=format&fit=crop&w=1050&q=80);
  background-size: 100% 100%;
  background-position: center;
  height: 1000px;
}
/*--Carrusel------------------------------------------------------------------------------*/
.carrusel{
  width:100% !important;
  margin: 0  !important;
  height: 80vh!important;
}
.carrusel-wrapper{
  transform: translateY(110px);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.4)
}
.slider-slide, .slider-list, .slider-frame{
  height: 100% !important;
}
.item-c{
  -webkit-background-size: cover !important;
  -moz-background-size: cover !important;
  -o-background-size: cover !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  height: 100% !important;
}
.item-c-1{
  background: url('https://images.unsplash.com/photo-1519389950473-47ba0277781c?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=47d2b1f32cc05c976af4a39256e01cba&auto=format&fit=crop&w=1050&q=80');
}
.item-c-2{
  background: url('https://images.unsplash.com/photo-1476703993599-0035a21b17a9?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=7a5314e43cef3e99c3849ed404a6d35b&auto=format&fit=crop&w=1050&q=80');
}
.item-c-3{
  background: url('https://images.unsplash.com/photo-1516044734145-07ca8eef8731?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=a481a304e9ab2ec5505b7b2d94157fc0&auto=format&fit=crop&w=1053&q=80');
}
.sobre-c{
  min-height: 400px;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: transparent;
  transition: 1s ease;
  z-index: 1;

}
/* .sobre-c:hover{
opacity: 0.8;
} */
.descr-sobre-c{
  position: absolute;
  font-size: 20px;
  top:80%;
  left:1%;
  background-color:rgba(38, 67, 144, 0.8);
  padding: 0 10px 0 10px;
  color: #fff;
  font-weight: normal;
  /* text-shadow: -1px 0 grey, 0 1px grey, 1px 0 grey, 0 -1px white; */
  font-family: "Monospaced Number", "Chinese Quote", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
/*--Homeinfo------------------------------------------------------------------------------*/

.homeinfo-categories-wrapper{
  transform: translateY(110px);
  display: flex;
  flex-wrap: wrap;
  padding: 0 40px 100px 40px;
  justify-content: space-around;
  align-items: center;
  background: white;
  border-bottom: 2px solid #3b3a8f;
}
.homeinfo-products-wrapper{
  transform: translateY(110px);
  display: flex;
  flex-wrap: wrap;
  padding: 0 40px 0 40px;
  justify-content: space-around;
  align-items: center;
  background: #fff;
}
.home-thumbnail{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.3);
  width: 30%;
  height: 25vw;
  margin-top: 20px;
  position: relative;
  transition: 0.3s;
  cursor: pointer;
  background-color: white;
}
.home-thumbnail:hover{
  box-shadow: 4px 12px 16px 0 rgba(0,0,0,0.3);
}
.home-thumbnail-pic{
  padding: none;
  height: 20vw;
  background-size: 100% 100% !important;
  background-repeat: no-repeat;
}

.home-thumbnail-des{
  padding: 14px 16px;
  text-align: center;
}
@media only screen and (max-width: 767px) {
  #search{
    top:65px;
    right:50%!important;
    transform: translate(50%)!important;
  }
  #search-input{
    transform:translateX(30px);
    width:30vw!important;
  }
  #search-buttom{
    transform:translateX(30px);
  }
  .navbar-img{
    width: 110px !important;
    height: 40px !important;
  }
  .homeinfo-wrapper{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: space-between;
  }
  .home-thumbnail{
    width: 400px;
    height: 300px;
  }
  .home-thumbnail-pic{
    height: 200px;
  }
}
@media only screen and (max-width: 440px) {
  .home-thumbnail{
    width: 250px;
    height: 300px;
  }
  .home-thumbnail-pic{
    height: 200px;
  }
}
.price .ant-card-meta-description {
    color: #bc5a5b !important;
    font-weight:bolder;
    font-size: 20px;
}
.old-price{

  font-size: 20px;
  color: black !important;
  text-decoration: line-through;
}
/*--Footer------------------------------------------------------------------------------*/
.footer{
  background-color: #3b3a8f;
  transform: translateY(110px);
  display: flex;
  flex-wrap: wrap;
  padding: 55px 90px;
  justify-content: space-around;
}
.footer-element{
  padding-right: 60px;
}
.footer-title{
  color:white;
  font-weight: bolder;
  font-size: 17px;
  padding-bottom: 15px;
}
.footer-sub{
  padding-top: 10px;

  font-weight: ;
  font-size: 10px;
  padding: 0;
  list-style-type: none;
}
.footer-sub>li>a{
  font-weight: lighter !important;
  color:#d6d6d6 ;
}
.footer-sub>li{
  font-weight: lighter !important;
  color:#d6d6d6 ;
}
.footer-sub>li>a:hover{
  color:#40a9ff ;
}
.social{
  font-size: 15px;
  transition: font-size 0.3s;
}
.social:hover{
    text-decoration: none;
    color: white !important;
    font-size: 20px;
  }
  .branding{
    background-color: #3b3a8f;
    transform: translateY(110px);
  }
  .branding-text{
    color: white;
    width: 100%;
    text-align: right;
    padding-right: 20px;
    display: block;
  }
  .branding-text:hover{
    color: white;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
  }
/*--------------------Products-----------------------------------*/
.meta{
  text-align: center;
}
.products-title{
  transform:translateY(110px);
  padding: 30px 0 0 80px ;
  width:30%;
  color: #3b3a8f;
  font-weight: bolder;
  font-size: 30px;
}
.products-title-home{
  transform:translateY(110px);
  padding: 40px 0 20px 0 ;
  width:30%;
  color: #3b3a8f;
  font-weight: bolder;
  font-size: 30px;
  text-align: center;
}
.products-wrapper{
  transform: translateY(110px);
  display: flex;
  flex-wrap: wrap;
  padding: 0 190px 100px 190px;
  justify-content: space-between;
  align-items: center;
}
.product{
  margin: 30px
}
.product:hover, .product:hover *{
  cursor: pointer;
  color: #c21b1c;
}
/*--------------------Product detail-----------------------------------*/
.product-detail{
  transform: translateY(110px);
  width: 85%;
  margin: 0 auto;
  padding: 60px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.product-detail-image{
  width: 400px;
  height: 50vh;
  background-size: 100% 100% !important;
  background-repeat: no-repeat !important;
  padding: 50px;
}
.product-detail-info{
  width: 650px;
  text-align: justify;

}

.product-detail-name{
  color:black;
  font-size: 30px;
  padding-bottom: 10px;
}
.product-detail-specs{
  color:black;
  padding-left: 20px;
  font-size: 12px;
}
.product-detail-price{
  padding-bottom: 10px;
  color:black;
  font-size: 25px;
  border-bottom: 1px solid #d3d3d3;
}
.product-detail-description{
  color:#515151;
  font-size: 20px;
  padding-bottom: 10px;
}
.product-detail-ch{
  color:#515151;
  font-size: 15px;
  padding-bottom: 10px;
  font-weight: lighter;
}
.product-detail-second-image{
  margin: 10px !important;

  background-size: 100% 100% !important;
  background-repeat: no-repeat !important;
  width:70px;
  height: 8vh;
}
.product-detail-second-image:hover{
  cursor: pointer;
}
.product-detail-second-images-wrapper{
  flex-wrap: wrap;
  display: flex;
}
.btn {
  border: 0;
  font-size: 1.5rem;
  height: 4rem;
  line-height: 2.5rem;
  padding: 0 1.5rem;
  color: white;
  background: #c21b1c;
  text-transform: uppercase;
  border-radius: .25rem;
  letter-spacing: .2em;
}
.btn:focus {
  outline: 0;
}
/*--------------------Contact-----------------------------------*/
.contact{
  display: flex;
  flex: wrap;
  justify-content: space-around;
}
.contact-label{
  display: inline;
  margin-right: 20px;
  font-size: 17px;
}
.contact-form{
}
.contact-form-item{
  display: block;
  margin-bottom: 20px;

}
